<template>
  <div id="pinglun">
    <div class="pinglun-heander">
      <h2 class="pinglun-h2">评论区</h2>
    </div>
    <div class="pinglun-con">
      <textarea v-model="shuru" class="pinglun-wenben"></textarea>
      <button v-on:click="send" class="pinglun-send">发布评论</button>
      <div class="pinglun-footer">
        <ul class="pinglun-ul">
          <li v-for="(v,i) in con" class="pinglun-li">
            {{v}}
            <span v-on:click="del(i)" class="pinglun-del"><i><img src="../assets/删除.png" alt=""></i></span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</div>
</template>
<script>
  export default {
    name: "pinglun",
    data(){
     return{
       shuru:"",
       con:[]
     }
   },
   methods:{
    send(){
      this.con.push(this.shuru);
    },
    del(i){
      this.con.splice(i,1);
    }
  }
}
</script>

<style scoped>
  *{
    margin: 0px;
    padding: 0px;
    list-style:none;
  }
  .pinglun-heander{
    width: 100%;
    background:#42b2fc;
    text-align: center;
  }
  .pinglun-h2{
    width: 100%;
    height: 60px;
    color:#fff;
    line-height:60px;
  }
  .pinglun-con{
    width: 100%;
    margin-top: 15px;
  }
  .pinglun-wenben{
    width: 100%;
    height: 100px;
    border-color:1px solid #42b2fc;
    outline:none;
    font-size: 16px;
  }
  .pinglun-send{
    width: 76px;
    height: 35px;
    font-size: 16px;
    background: #42b2fc;
    outline: none;
    float: right;
  }
  .pinglun-footer{
    width: 100%;
    margin-top: 90px;
    margin-bottom:50px;
  }
  .pinglun-ul{
    width: 100%;
  }
  .pinglun-li{
    width: 100%;
    border-bottom:1px solid #ccc;
    margin-bottom: 25px;
    position: relative;

  }
  .pinglun-del{
    float:right;
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 0px;
    right: 0px;
  }
  .pinglun-del i{
    width: 100%;
    height: 100%;
  }
  .pinglun-del i img{
    width: 100%;
    height: 100%;
  }
</style>



