<template>
 <div id="box">
  <mt-tabbar v-model="selected"  class='footer'>     
    <mt-tab-item id="首页" class='footer-p footer-li'>
      <img slot="icon" src="../assets/images/index.png">
      <router-link to='/App'><span>首页</span> </router-link>
    </mt-tab-item>    
    <mt-tab-item id="热门" class='footer-p footer-li'>
      <img slot="icon" src="../assets/images/热门.png">
      <router-link to='/hot'>  <span>热门</span></router-link>
    </mt-tab-item>      
    <mt-tab-item id="评论" class='footer-p footer-li'>
      <img slot="icon" src="../assets/images/评论.png">
      <router-link to='/comment'> <span>评论</span> </router-link>
    </mt-tab-item>     
    <mt-tab-item id="我的" class='footer-p footer-li' @click="back">
      <img slot="icon" src="../assets/images/我的.png">
      <router-link to='/mine'><span>我的</span>  </router-link>
    </mt-tab-item>    
  </mt-tabbar>
</div>
</template>
<script>
  export default {
    name: 'box',
    data () {
      return {
        selected:"首页"
      }
    },
    methods:{
      back(){
        this.selected="首页"
      }
    }
  }
</script>
<style scoped>
  *{
    margin: 0px;
    list-style: none;
    padding: 0;
  }
  a{
    text-decoration: none;
  }
  .footer{
    width: 100%;
    height: 45px;
    background: #42b3fc;
    z-index: 5;
    bottom: 0px;
    position: fixed;
  }
  .footer-li{
    width: 25%;
    height: 100%;
    text-align: center;
    float: left;
    cursor: pointer;
  }
  .footer-p{
    color: #fff;
    font-size: 10px;
  } 
  span{
    color: #fff;
  }
  .mint-tab-item.is-selected {
    background-color: #2b97bc;
  }
</style>



