<template>
  <main id="hot">
    <div class="hot-heander">
      <h2 class="hot-h2">热门推荐</h2>
    </div>
    <div class="hot-con">
     <ul class="hot-ul">
       <li class="hot-li" v-for="(v,i) in news">
         <p class="hot-title">{{v.title}}</p>
         <div class="hot-img"><img :src="v.image" alt=""></div>
         <p class="hot-footer">
           <span class="hot-time">时间:{{v.pubdate}}</span>
           <span class="hot-laiyuan">来源:{{v.copyright}}</span>
         </p>
       </li>
     </ul>
   </div>
 </main>
</template>
<script>
  export default {
    name: "hot",
    data(){
      return {
        news:[]
      }
    },
    mounted(){
      // var url='/src/static/news.json';
      var url='http://api.komavideo.com/news/list';
      this.$axios.get(url)
      .then(response=>{
        console.log(response.data)
        this.news=response.data;
      })
      .catch(error=>{
        console.log(error);
      })
    }
  }
</script>

<style scoped>
  *{
    margin: 0px;
    padding: 0px;
    list-style:none;
  }
  .hot-heander{
    width: 100%;
    background:#42b2fc;
    text-align: center;
  }
  .hot-h2{
    width: 100%;
    height: 60px;
    color:#fff;
    line-height:60px;
  }
  .hot-con{
    width: 100%;
  }
  .hot-ul{
    width: 100%;
  }
  .hot-li{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #cccccc;
    margin-bottom: 60px;
  }
  .hot-title{
    width: 100%;
    color: #000;
    font-size: 20px;
    margin-bottom: 5px;
  }
  .hot-img{
    width: 100%;
    height: 200px;
    text-align: center;
    margin-bottom: 10px;
  }
  .hot-img img{
    width: 100%;
    height: 100%;
  }
  .hot-footer{
    width: 100%;
    margin-bottom: 5px;
    margin-top: 25px;
    flex-wrap: wrap;
    font-size: 12px;
  }
  .hot-time{
    float: left;
  }
  .hot-laiyuan{
    float: right;
  }
</style>



