<template>
  <main id="comment">
    <div class="com-heander">
      <h2 class="com-h2">评论主题</h2>
    </div>
    <div class="com-con">
      <div class="com-show">
        <ul class="show-ul">
          <li class="show-li" v-for="(v,i) in news">
            <div class="show-img">
              <img :src="v.image" alt="">
              <p class="show-p">{{v.title}}</p>
            </div>

            <div class="show-pinglun">
              <span class="show-left">评论:</span>
              <router-link to="/pinglun"><i class="show-right"><img src="../assets/评论5.png" alt=""></i> </router-link>
            </div>

          </li>
        </ul>
      </div>
    </div>
  </main>
</template>

<script>
  export default {
    name: "comment",
    data(){
      return{
        news:[]
      }
    },
    mounted(){
      var url='http://api.komavideo.com/news/list';
      this.$axios.get(url)
      .then(response=>{
        console.log(response.data)
        this.news=response.data;
      })
      .catch(error=>{
        console.log(error);
      })
    }
  }
</script>

<style scoped>
  *{
    margin: 0px;
    padding: 0px;
    list-style:none;
  }
  .com-heander{
    width: 100%;
    background:#42b2fc;
    text-align: center;
  }
  .com-h2{
    width: 100%;
    height: 60px;
    color:#fff;
    line-height:60px;
  }
  .com-con{
    width: 100%;
    margin-top: 10px;
    margin-bottom: 56px;
  }
  .com-show{
    width: 100%;
  }
  .show-ul{
    width: 100%;
  }
  .show-li{
    width: 100%;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
  }
  .show-img{
    width: 100%;
    height: 200px;
    text-align:center;
    margin-bottom: 40px;
  }
  .show-img img{
    width: 100%;
    height: 100%;
  }
  .show-p{
    position: relative;
    z-index: 2;
    bottom: 60px;
    width: 100%;
    text-align: left;
    background: #000;
    opacity: 0.5;
    color: #fff;
    height: 60px;
    font-size: 18px;
  }
  .show-pinglun{
    width: 100px;
    height: 40px;
    position: absolute;
    right: 10px;
    margin-top: -37px;
  }
  .show-left{
    width: 47px;
    height: 100%;
    line-height: 40px;
    font-size: 20px;
    position: absolute;
  }
  .show-right{
    float: right;
    height: 100%;
  }
  .show-right img{
    width: 100%;
    height: 100%;
  }
</style>



