import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import axios from 'axios'
Vue.prototype.$axios=axios;
import router from './js/router.js'
import Mint from 'mint-ui';
import '../node_modules/mint-ui/lib/style.css'
Vue.use(Mint);
Vue.use(VueRouter)
new Vue({
  el: '#app',
  render: h => h(App),
  router:router
})



