<template>
	<main id='index'>
		<div class="header">
			<i class="icont"><img src="../assets/images/01.png" class="pt"></i>
			<input type="text" name="" class="sousuo" value="大家都在搜">
			<div class="nav">
				<ul>
					<li v-for="(v,i) in news" class="lis">
						<span @click="now=i"><div @click="hidden(i)">{{v}}</div></span>
					</li>
				</ul>
			</div>
		</div>
		<div :class="{slide:del}">
			<div :class="{slideshow:del}">
				<mt-swipe :auto="4000">
					<mt-swipe-item><img src="../assets/1.jpg" class="lunbo"></mt-swipe-item>
					<mt-swipe-item><img src="../assets/2.jpg" class="lunbo"></mt-swipe-item>
					<mt-swipe-item><img src="../assets/3.jpg" class="lunbo"></mt-swipe-item>
					<mt-swipe-item><img src="../assets/4.jpg" class="lunbo"></mt-swipe-item>
					<mt-swipe-item><img src="../assets/5.jpg" class="lunbo"></mt-swipe-item>
				</mt-swipe>
			</div>			
		</div>
		<div class="con">
			<ul class="con-ul">
				<li class="con-li" v-for="(s,y) in xinwen">
					
						<img :src="s.image" alt="" class="con-tp">
						<div class="con-footer">
							<h1 class="title">{{s.title}}</h1>
							<p class="con-p">
								<span class="look">浏览：{{s.copyright}}</span>
								<span class="con-time">时间：{{s.pubdate}}</span>
								<span class="con-source">来源：{{s.copyright}}</span>
							</p>
						</div>
					
				</li>
			</ul>
		</div>
	</main>
</template>
<script>
import jsonp from 'jsonp'
	export default {
		name: 'index',
		data () {
			return {
				xinwen:[],
				now:1,
				news:["头条","娱乐","体育","健康","游戏","生活"],
				del:true
			}
		},
		methods:{
			hidden(i){
				if(i !=0){
					this.imgs="";
					this.del=false;
				}
				else{
					this.imgs=["src/assets/1.jpg",
					"src/assets/2.jpg",
					"src/assets/3.jpg",
					"src/assets/4.jpg",
					"src/assets/5.jpg"
					]
					this.del=true;
				}
			}

		},
		mounted(){
			var url='http://api.komavideo.com/news/list';
			this.$axios.get(url)
			.then(response=>{
				this.xinwen=response.data;
			})
			.catch(error=>{
				console.log(error);
			})
		}
	}

</script>
<style scoped>
	*{
		margin: 0px;
		list-style: none;
		padding: 0;
	}
	.header{
		width: 100%;
		height: 100px;
		background: #42b3fc;
		padding-top: 25px;
	}
	.sousuo{
		width: 90%;
		height: 30px;
		display: flex;
		border-radius: 20px;
		margin-left: 50%;
		margin-right: 50%;
		margin: auto;
		border: 1px solid #fff;
		outline:none;
		text-indent:2em;
		font-size: 1em;
	}
	.icont{
		width: 30px;
		height: 30px;
		display: flex;
		margin-left: 5%;
		margin-top: 3px;
		position: absolute;
	}
	.pt{
		width: 100%;
	}
	.nav{
		width: 100%;
		height: 37px;
		margin-top: 20px;
		color: #fff;
		line-height: 37px;

	}
	.lis{
		width: 16.6%;
		display: flex;
		float: left;
		cursor: pointer;
	}
	.lis span{
		width: 100%;
		text-align: center;
	}
	.lis span::after{
		content: "";
		display: block;
		position: relative;
		width: 64px;
		height: 3px;
		background-color:#fff;
		display: none;
	}
	.slide {
		width: 100%;
		height: 200px;
		overflow: hidden;
		position: relative;
	}
	.slideshow {
		width: 100%;
		height: 200px;
	}	
	.con{
		width: 100%;
		margin-top: 20px;
	}
	.con-ul{
		width: 100%;

	}
	.con-a{
		width: 100%;
		text-decoration: none;
	}
	.con-li{
		width: 100%;
		/* height: 150px; */
		display: flex;
		position: relative;
		border-bottom: 1px solid #999;
	}
	.title{
		width: 100%;
		/* height: 50%; */
		font-size: 16px;
		color: #333;
		word-break: break-all;
    white-space: normal;
    overflow: hidden;
	}
	.con-tp{
		width: 35%;
		height: 92%;
		position: absolute;
		top: 20px;
		right: 0px;
		height: 55%;
	}
	.con-li span{
		font-size: 10px;
		margin-right: 5px;
	}
	.con-footer{
		width: 63%;
		display: flex;
		flex-wrap: wrap;
		margin-top: 5px;
		margin-bottom: 60px;
	}
	.con-p{
		width: 100%;
		height: 20%;
		display: flex;
		flex-wrap: wrap;
		color: #666;
	}
	.lunbo{
		width: 100%;
		height: 100%;
	}
</style>



